export function getMarkup(message, username, color) {
    return `<button style="background:${color};display:flex;align-items:center;color:#fff;border:1px solid transparent;padding:.37rem .7rem;border-radius:.25rem;"onclick="openWin()"><img style="width:32px;height:40px;margin-right:1rem;"src="https://svgur.com/i/Zry.svg"/>${message}</button><script>function openWin(){window.focus&&window.open("https://revolut.me/${username}",null,["width=500","height=600","top="+(window.screen.height-600)/2,"left="+(window.screen.width-500)/2,"directories=no","location=no","menubar=no","resizable=no","scrollbars=no","status=no","toolbar=no"].join(",")).focus()}</script>`
}

export function preview(username) {
    let url = `https://revolut.me/${username}`
    let width  = 500;
    let height = 600;
    let left   = (window.screen.width  - width)/2;
    let top    = (window.screen.height - height)/2;

    if (window.focus) {
         window.open(url, null, [
            `width=${width}`,
            `height=${height}`,
            `top=${top}`,
            `left=${left}`,
            'directories=no',
            'location=no',
            'menubar=no',
            'resizable=no',
            'scrollbars=no',
            'status=no',
            'toolbar=no'
        ].join(',')).focus();
    }
}