import React from 'react';

import '../Styles/Create.scss';

export default function Create(props) {
    return <div className="col-xs-12 col-md-10">
        <form id="create-component">
            <div className="mb-5">
                <h3>Create your button</h3>
                <p className="text-muted">The important stuff is handled by revolut.</p>
            </div>

            <div className="mb-4">
                <div className="input-group">
                    <span className="input-group-text">revolut.me/</span>
                    <input
                        type="text"
                        className="form-control"
                        defaultValue={props.formData.username}
                        onChange={(e) => props.setFormData({...props.formData, username: e.target.value })} />
                </div>
                <div className="d-flex flex-column mt-2">
                    <a class="form-text text-primary text-decoration-none" target="_blank" href="https://www.revolut.com/">What is Revolut?</a>
                    <a class="form-text text-primary text-decoration-none" target="_blank" href="https://www.revolut.com/en-AT/help/making-payments/sending-money-to-another-revolut-account/username-payments/how-can-i-send-money-by-username">I canâ€™t find my revolut username</a>
                </div>
            </div>
            <div className="mb-4">
                <label className="form-label">Message</label>
                <input
                    type="text"
                    className="form-control"
                    placeholder="Buy me a coffe"
                    defaultValue={props.formData.message}
                    onChange={(e) => props.setFormData({...props.formData, message: e.target.value})} />
            </div>

            <div className="colors mb-4">
                <div className="d-flex">
                    <label className="form-label">Background color</label>
                    <span className="black" onClick={() => props.setFormData({...props.formData, color: '#000000'})}></span>
                    <span className="blue" onClick={() => props.setFormData({...props.formData, color: '#0d6efd'})}></span>
                </div> 
            </div>
        </form>
    </div>
}